.header {
  background-color: rgb(226, 226, 226);
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.left {
  flex-basis: 50%;
  width: 35%;
  display: flex;
  justify-content: space-around;
}
.left div {
  display: flex;
}
.left label {
  color: rgb(76, 76, 76);
  font-size: 1.6em;
}
.right {
  padding-right: 10px;
  width: 30%;
}
.logo {
  margin-right: 4px;
  height: 30px;
}
.homeDiv {
  width: 98.5%;
  display: flex;
  margin-top: 10px;
  padding-left: 0.75%;
}

@media screen and (max-width: 800px) {
  .homeDiv {
    flex-wrap: wrap;
  }
  .left {
    flex-basis: 100%;
  }
  .right {
    flex-basis: 100%;
    position: relative;
    top: 646px;
    z-index: 1;
  }
}
