.queryDrop {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #e4e4e4;
  border-radius: 4px;
  background-color: white;
  color: #333;
  cursor: pointer;
  width: 45%;
}
.queryDrop option {
  background-color: #f5f5f5;
}

@media screen and (max-width: 800px) {
  .queryDrop {
    width: 20%;
  }
}
