.queryInputHolder {
  display: flex;
  height: 85vh;
  width: 100%;
  min-width: 22rem;
  max-width: 100%;
  border: 1px solid #ccc;
  position: relative;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.lineNumbers {
  font-family: inherit;
  text-align: center;
  color: #757575;
  background-color: #d6d6d6;
  width: fit-content;
  min-width: fit-content;
  overflow-y: auto;
  overflow-x: hidden;
  padding-top: 16px;
}
.lineNumbers::-webkit-scrollbar {
  display: none;
}
.queryInput {
  color: #4f4f4f;
  font-family: inherit;
  font-weight: 600;
  padding-top: 16px;
  padding-left: 10px;
  min-width: 18.3rem;
  width: 45rem;
  border: none;
  resize: none;
  overflow: auto;
  white-space: nowrap;
}
.queryInput:focus {
  outline: none;
}
.resizeHandle {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;
  font-size: 1.5em;
  width: 10px;
  margin-right: -2px;
  background-color: #9f9f9f;
  cursor: e-resize;
}
.resizeHandle:hover {
  background-color: #4f4f4f;
}
.resizeHandle p {
  margin: 0;
  margin-top: -20px;
  padding: 0;
}

@media screen and (max-width: 800px) {
  .resizeHandle {
    display: none;
  }
  .queryInputHolder {
    background-color: rgb(255, 255, 255);
    height: 635px;
    min-width: 100%;
  }
  .queryInput {
    background-color: rgb(255, 255, 255);
    width: 100%;
    min-width: 90%;
  }
}
